.App {
  text-align: center;
  height: 100%;
}


.App-header {
  background-color: #282c34;
  min-height: 79vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.stickyfooter {
  position: sticky;
  bottom: 0;  
  background: #C0C0C0;
    height: 85px;
    display: flex;
    justify-content: space-between;
    padding: 0.2rem calc((100vw - 1000px) / 2);
    z-index: 12;
}
